.checkListPage{
    width: fit-content;
}

.checkListBox{
    margin: 0%;
    border: 12px double #FF2DD1;
    background-color: #FED2E2;
    padding: 2%;
}

.checkListCircle{
    font-size: xx-large;
    text-align: center;
    margin: 0%;
    color: #0065F8;
}

.checkListHead{
    font-size: x-large;
    color: #4E31AA;
    text-align: center;
    font-style: italic;
    
}

.listItem{
    margin: 2.5% auto;
    font-size: large;
}

.updateBtn{
    padding: 2%;
    border-radius: 20px;
    background-color: black;
    color: white;
    cursor: pointer;
    font-weight: bold;
}

.checkListInp{
    cursor: pointer;
}


/* MEDIA QUERIES */

/* Tablet (≤768px) */
@media screen and (max-width: 768px) {
  .checkListPage {
    width: 90%;
    margin: 2% auto;
  }

  .checkListBox {
    padding: 4%;
  }

  .checkListHead {
    font-size: large;
  }

  .listItem {
    font-size: medium;
  }

  .updateBtn {
    width: 100%;
    padding: 3%;
    font-size: medium;
  }
}

/* Mobile (≤480px) */
@media screen and (max-width: 480px) {
  .checkListPage {
    width: 95%;
    margin: 5% auto;
  }

  .checkListBox {
    border-width: 8px;
    padding: 5%;
  }

  .checkListCircle {
    font-size: x-large;
  }

  .checkListHead {
    font-size: medium;
  }

  .listItem {
    font-size: small;
  }

  .updateBtn {
    width: 100%;
    padding: 4%;
    font-size: small;
  }
}
