.locationPage{
    margin: 10% auto;
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.locationhead{
    display: block;
    color: rgba(74, 74, 245, 0.733);
}

.locationIcon{
    width: 30%;
    text-align: center;
}

.rightSide{
    width: 60%;
    margin: 2% auto;
}

.locationForm{
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid black;
    padding: 2%;
    background-color: #EAE4D5;
}

.locationPageLabel{
    font-size: x-large;
}

.locationPageInp{
    width: 50%;
    border-radius:15px ;
    font-size: large;
    padding: 1%;
}

.locationPageBtn{
    padding: 2%;
    border-radius: 25px;
    font-size: medium;
    background-color: black;
    color: aqua;
    cursor: pointer;
}

.backBtnDiv{
    width: 40%;
    margin: 2% auto;
}

.backBtn{
    padding: 2%;
    width: 100%;
    border-radius: 5px;
    font-size: medium;
    background-color: black;
    color: gold;
    cursor: pointer;
}


/* Media Queries */
/* Tablet (≤768px) */
@media screen and (max-width: 768px) {
  .locationPage {
    flex-direction: column;
    align-items: center;
    width: 90%;
    gap: 1.5rem;
  }

  .locationIcon {
    width: 50%;
    margin-bottom: 1rem;
  }

  .rightSide {
    width: 100%;
  }

  .locationForm {
    flex-direction: column;
    gap: 1rem;
  }

  .locationPageInp {
    width: 90%;
  }

  .backBtnDiv {
    width: 70%;
  }
}

/* Mobile (≤480px) */
@media screen and (max-width: 480px) {
  .locationPage {
    width: 95%;
    margin: 5% auto;
    flex-direction: column;
  }

  .locationIcon {
    width: 70%;
  }

  .rightSide {
    width: 100%;
  }

  .locationForm {
    padding: 5%;
    flex-direction: column;
    gap: 0.8rem;
  }

  .locationPageInp {
    width: 100%;
    font-size: medium;
  }

  .locationPageBtn,
  .backBtn {
    width: 100%;
    font-size: small;
  }

  .backBtnDiv {
    width: 90%;
  }
}
