.exerciseContainer{
    margin-top: 10%;
    border: 5px outset #113F67;
    padding: 1%;
    background-color: #58A0C8;
    display: block;
    position: fixed;
    width: 100%;
}

.msgDiv{
    padding: 5%;
    font-size: x-large;
    font-family: 'Times New Roman', Times, serif;
    color: red;
}

.btnDiv{
    width: 70%;
    margin: 1% auto;
}

.exerciseBtn{
    font-size: medium;
    border-radius: 50%;
    padding: 5%;
    border: 1px solid #ccc;
    color: #03A6A1;
    background-color: #AEEA94;
}