.navMenuPage{
    background-color: #ADEED9;
    
    position: absolute;
    width: 12rem;
    top: 3.5rem;
    right: 2.5rem;


}

.navMenuBtn{
    display:block;
    width: 100%;
    background-color: transparent;
    padding: 5%;
    border: none;
    font-size: large;
    color: #1E201E;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
    font-weight: bold;
}


/* MEDIA QUERIES */

@media screen and (max-width: 768px) {
  .navMenuPage {
    right: 1rem;
    width: 10rem;
  }

  .navMenuBtn {
    font-size: medium;
  }
}