/* HOME PAGE */

.homePage{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 5% 0%;
}

.pageLeftSection{
    width: 60%;
}

.pageRightSection{
    width: 15%;
    
}


/* WEATHER APP */

.weatherSection{

    display: block;
    position: fixed;
    z-index: -1;
    top:7%;
    

    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: large;
    padding: 2%;
    border-radius: 5px;
    border: 7px inset #03A6A1;;
    text-align: center;
    background-color: #ffe3bb98;

    
}

.weatherBottomSection{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.largeLabel{
    font-size: xx-large;
}

.locationSection{
    margin: 10% 0%;
    font-size: medium;
}


/* FORM PAGE CSS */

.formContainer{
    font-size: larger;
    border: 1px solid red;
    box-shadow: 20px 20px 10px 0px rgba(247, 74, 6, 0.726);
}

.formHead{
    font-weight: bold;
    color: blue;
    font-size: x-large;
    
}

.formInputDiv{
    display: flex;
    align-items: center;
    margin: 2% 0%;
}

.formInputDiv label{
    flex: 1;
}

.inpBox{
    /* flex: 2; */
    width: 50%;
    padding: 1%;
    border-radius: 10px;
}

.option1{
    right: 2%;
}

.submitBtn{
    border-radius: 5px;
    padding: 1%;
    width: 20%;
    background-color: #000000;
    color: #FFFCFB;
    font-weight: bold;
    cursor: pointer;
    font-size: medium;
    margin: 1% auto;
}


/* Form Response */
.responsePage{
    font-size: large;
    margin: 10% auto;
}


.test{
    margin: 20%;
}


/* MEDIA QUERIES */

/* Tablet (≤768px) */
@media screen and (max-width: 768px) {
  .homePage {
    flex-direction: column;
    align-items: stretch;
    margin: 5% 2%;
  }

  .pageLeftSection {
    width: 100%;
  }

  .pageRightSection {
    width: 100%;
    position: relative;
    margin-top: 2rem;
  }

  .weatherSection {
    position: relative;
    width: 100%;
    padding: 4%;
    font-size: medium;
  }

  .formContainer {
    font-size: medium;
    padding: 5%;
  }

  .formHead {
    font-size: large;
  }

  .formInputDiv {
    flex-direction: column;
    align-items: flex-start;
  }

  .formInputDiv label {
    margin-bottom: 0.5rem;
    width: 100%;
  }

  .inpBox {
    width: 100%;
  }

  .submitBtn {
    width: 50%;
    font-size: medium;
  }

  .responsePage {
    font-size: medium;
    margin: 5% auto;
  }

  .largeLabel {
    font-size: x-large;
  }

  .weatherBottomSection {
    flex-direction: row;
    gap: 10px;
    justify-content: center;
  }

  .locationSection {
    font-size: small;
  }
}

/* Mobile (≤480px) */
@media screen and (max-width: 480px) {
  .homePage {
    flex-direction: column;
    margin: 5% 2%;
  }

  .pageLeftSection {
    width: 100%;
  }

  .pageRightSection {
    width: 100%;
    position: relative;
    margin-top: 2rem;
  }

  .weatherSection {
    position: relative;
    padding: 5%;
    font-size: small;
  }

  .largeLabel {
    font-size: large;
  }

  .formContainer {
    font-size: small;
    padding: 5%;
  }

  .formHead {
    font-size: medium;
  }

  .formInputDiv {
    flex-direction: column;
    align-items: flex-start;
  }

  .formInputDiv label {
    margin-bottom: 0.5rem;
    width: 100%;
  }

  .inpBox {
    width: 100%;
    font-size: small;
  }

  .submitBtn {
    width: 70%;
    font-size: small;
  }

  .responsePage {
    font-size: small;
    margin: 5% auto;
  }

  .weatherBottomSection {
    flex-direction: column;
    gap: 0.5rem;
  }

  .locationSection {
    font-size: smaller;
    flex-direction: column;
    align-items: center;
  }

  .locationSection img {
    width: 20%;
    height: auto;
  }
}
