.welcomePage{
    /* width: 80%; */
    margin: 4% auto;
    background-image: url("https://thumbs.dreamstime.com/b/two-hands-present-red-first-aid-kit-marked-white-cross-against-pastel-blue-backdrop-greenery-holding-background-348886834.jpg");
    background-size: cover;
    width: 100vw;
    height: 100vh;
}

.welcomePageHead{
    text-align: center;
    color: red;
    font-family: 'Times New Roman', Times, serif;
    font-size: xx-large;
    font-weight: bold;
}

.welcomePageTagline{
    text-align: center;
    font-size: x-large;
    font-family: 'Times New Roman', Times, serif;
    color: #FF7F3E;
}

.introParas{
    width: 50%;
    float: right;
    margin: 5%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: larger;
    line-height: 1.6;
}

.para1,.para2{
    margin: 6% auto;
}

.para1{
    text-align: center;
    color: #0C0950;
}

.para2{
    font-size: x-large;
    font-style: italic;
    color: #D20062;
}


/* MEDIA QUERIES */

/* ===== MOBILE DEVICES (max-width: 600px) ===== */
@media screen and (max-width: 600px) {

  .signUpPage {
    flex-direction: column;
    margin: 2% auto;
  }

  .imgSection {
    width: 80%;
    margin-bottom: 20px;
  }

  .formSection {
    width: 90%;
  }

  .formInput {
    width: 90%;
    font-size: small;
    padding: 2%;
  }

  .signUpBtn {
    width: 100%;
    padding: 3%;
  }

  .introParas {
    width: 90%;
    float: none;
    margin: 10% auto;
    font-size: medium;
  }

  .para1, .para2 {
    margin: 8% auto;
    font-size: medium;
  }

  .welcomePageTagline {
    font-size: large;
  }

  .welcomePageHead {
    font-size: x-large;
  }
}


/* ===== TABLETS (601px to 1024px) ===== */
@media screen and (min-width: 601px) and (max-width: 1024px) {

  .signUpPage {
    flex-direction: column;
    margin: 4% auto;
  }

  .imgSection {
    width: 60%;
    margin-bottom: 20px;
  }

  .formSection {
    width: 85%;
  }

  .formInput {
    width: 80%;
    font-size: medium;
    padding: 2%;
  }

  .signUpBtn {
    width: 60%;
    padding: 2%;
  }

  .introParas {
    width: 80%;
    float: none;
    margin: 8% auto;
    font-size: large;
  }

  .para1, .para2 {
    margin: 6% auto;
  }

  .welcomePageTagline {
    font-size: x-large;
  }

  .welcomePageHead {
    font-size: xx-large;
  }
}

